.Contacts {
    grid-column: 3;
    grid-row: 3;

    display: flex;
    align-items: center;

    width: 100%;
    height: 100%;
}

.telegram {
    width: 35px;
    height: 35px;

    margin-right: 20px;

    background: url('../../img/telegram.svg') center center/contain no-repeat;
}



@media screen and (max-width: 1440px){
    .Contacts {
        grid-column: 1/-1;
        justify-content: center;
    }
}


@media screen and (max-width: 900px){
    .Contacts {
        grid-column: 1/-1;
        grid-row: -1/-2;
        justify-content: center;
    }
}

@media screen and (max-width: 900px){
    .Contacts {
        font-size: 12px;
    }
}