.Nav {
    grid-column: 4;
    grid-row: 2;

    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    
    padding: 0;
    margin: 0;
    list-style: none;
}

.Nav li {
    position: relative;
    cursor: pointer;
    line-height: 40px;
}

.Nav li.active::before {
    position: absolute;
    content: '';
    left: -20px;
    top: 50%;

    width: 10px;
    height: 10px;

    transform: translateY(-50%);

    background-color: white;
}


@media screen and (max-width: 1440px) {
    .Nav {
        grid-column: 1/-1;
        grid-row: 1;

        flex-direction: initial;
        align-items: center;
    }

    .Nav li {
        margin-right: 60px;
    }

    .Nav li:last-child {
        margin-right: 0;
    }
}

@media screen and (max-width: 768px) {
    .Nav {
        grid-column: 2/-2;
        grid-row: 1;

        position: relative;

        flex-wrap: wrap;

        overflow-x: auto;
        z-index: 10;
    }

    .Nav li {
        line-height: 20px;
        white-space: nowrap;
        margin-right: 20px;
    }

    .Nav li.active {
        color: #66b132;
    }

    .Nav li.active::before {
        display: none;
    }
}
