.App {
    height: 100vh;

    display: grid;
    grid-template-columns: 5% 30% 35% 15%;
    grid-template-rows: 15% 70% 15%;
    grid-column-gap: 5%;
}


.Smile {
    position: relative;
    left: 0;
    top: 0;

    grid-column: 2;
    grid-row: 2;

    width: 100%;
    height: 100%;
}



@media screen and (max-width: 1440px) {
    .App {
        grid-template-columns: repeat(12, 1fr);
        grid-column-gap: 0;
    }

    .Smile {
        grid-column: 2/6;
    }

}


@media screen and (max-width: 900px) {
    .App {
        grid-template-rows: repeat(12, 1fr);
    }

    .Smile {
        grid-column: 2/12;
        grid-row: 2/-2;
    }

}

@media screen and (max-width: 768px) {
    .Smile {
        grid-column: 1/-1;
        grid-row: 2/-2;
    }
}


