.CurrentWork {
    position: relative;
    left: 0;
    top: 0;

    grid-column: 2;
    grid-row: 2;

    width: 100%;
    height: 100%;

    font-size: 18px;

    z-index: 10;
    background-color: rgba(0, 0, 0, 0.75);
}

.title {
    font-size: 40px;
    margin-bottom: 20px;
}

.tasks {
    margin-top: 20px;

    font-size: 24px;
}

li {
    line-height: 34px;
}

.link {
    display: block;
    margin-top: 40px;

    color: white;
    text-decoration: none;
    cursor: pointer;
}


.comment p {
    margin: 0;
}


@media screen and (max-width: 1440px) {
    .CurrentWork {
        font-size: 16px;
    }

    .title {
        font-size: 30px;
    }

    li {
        font-size: 16px;
        line-height: 22px;
    }

    .comment p {
        display: inline;
    }

    .tasks {
        margin-top: 10px;
        font-size: 16px;
    }
}


@media screen and (max-width: 1440px) {
    .CurrentWork {
        grid-column: 2/8;
    }
}

@media screen and (max-width: 900px) {
    .CurrentWork {
        grid-column: 2/12;
        grid-row: 4/-2;
    }
}

@media screen and (max-width: 768px) {
    .CurrentWork {
        grid-column: 1/-1;
        grid-row: 4/-2;
        padding: 0 20px;
        overflow-y: auto;
    }

    .link {
        margin-bottom: 40px;
    }
}

