.Skills {
   position: relative;
   width: 100%;
   height: 100%;
}

.Skills .header {
   display: flex;
   height: 10%;
   align-items: center;
}

.headerItem {
   width: 25%;
   text-align: center;
}


.Skills .table {
    width: 100%;
    height: 80%;

    overflow-y: scroll;
}


.Skills .table::-webkit-scrollbar {
    width: 0;
}

.Skills .texnology {
    display: flex;
}

.Skills .name {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 25%;
    height: 7vh;
}


.Skills .progressBar {
    display: flex;
    align-items: center;
    justify-content: space-around;

    width: 75%;
}


.Skills .cell {
    height: 12px;
    width: 30%;
    border: 1px solid white;
}

.Skills .cell.l1 {
    background-color: #FEF200;
}

.Skills .cell.l2 {
    background-color: #b3df16;
}

.Skills .cell.l3 {
    background-color: #66B132;
}


.Skills .footer {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 10%;
}

@media screen and (max-width: 768px) {
    .Skills {
        font-size: 14px;
    }
}

@media screen and (max-width: 420px) {
    .Skills {
        font-size: 12px;
    }
}
