.Works {
   display: grid;
   grid-template-columns: repeat(2, 1fr);
   grid-template-rows: repeat(3, 1fr);
   gap: 2.5%;

   width: 100%;
   height: 100%;
}


.Works .card {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 34px;
    text-align: center;

    border: 1px solid white;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    cursor: pointer;
}

.Works .card span {
    position: relative;
    padding: 0 20px;
    z-index: 1;
}


.Works .card::before {
    content: '';

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

.Works .card.active {
    color: #66B132;
    border: 1px solid #66B132;
}


@media screen and (max-width: 1440px) {
    .Works {
        display: flex;
        flex-direction: column;
    }

    .Works .card {
        height: 33%;
        font-size: 24px;
    }
}

@media screen and (max-width: 900px) {
    .Works {
       flex-direction: row;
    }

    .Works .card {
        height: 100%;
        white-space: nowrap;
    }
}

@media screen and (max-width: 768px) {
    .Works {
        align-items: center;
    }

    .Works .card {
        height: 50%;
        white-space: nowrap;
    }
}