.Content {
    grid-column: 3;
    grid-row: 2;
}


@media screen and (max-width: 1440px) {
    .Content {
        grid-column: 7/12;

        display: flex;
        align-items: center;
    }

    .Content.Works {
        grid-column: 9/12;
    }
}



@media screen and (max-width: 900px) {
    .Content {
        grid-column: 2/12;
        grid-row: 2/-2;

        position: relative;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 10;
    }

    .Content.Works {
        grid-row: 2;
        grid-column: 2/12;
        overflow-x: auto;
    }

    .Content::-webkit-scrollbar {
        display: none;
        width: 0;
    }
}

@media screen and (max-width: 768px) {
    .Content {
        grid-column: 1/-1;
        grid-row: 2/-2;

        padding: 0 20px;

        overflow-y: auto;
    }

    .Content.Works {
        grid-column: 1/-1;
        grid-row: 2/4;
    }
}